<template>
  <div>
    <el-card class="box-card">
      <el-col :span="24">
        <el-row>
          <el-col :span="24">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
              <el-form-item label="流水号">
                <el-input v-model="formInline.value1" clearable placeholder="根据流水号查询"></el-input>
              </el-form-item>
              <el-form-item label="证件号码">
                <el-input v-model="formInline.value2" clearable placeholder="根据证件号码查询"></el-input>
              </el-form-item>
              <el-form-item label="审核状态">
                <el-select v-model="formInline.value3" clearable placeholder="根据审核状态筛选">
                  <el-option label="正在检测" value="正在检测"></el-option>
                  <el-option label="等待审核" value="等待审核"></el-option>
                  <el-option label="审核通过" value="审核通过"></el-option>
                  <el-option label="审核失败" value="审核失败"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="审核人员">
                <el-input v-model="formInline.value4" clearable placeholder="根据审核人员查询"></el-input>
              </el-form-item>
              <el-form-item label="体检机设备编号">
                <el-input v-model="formInline.value5" clearable placeholder="根据设备编号查询"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="openVisible5">体检信息记录下载</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="24" style="margin-top:10px;">
            <el-table :data="tableData" border onselectstart="return false">
              <el-table-column fixed width="50" prop="lists" label="序号" align="center"></el-table-column>
              <el-table-column width="130" prop="serial" label="流水号" align="center"></el-table-column>
              <el-table-column width="80" prop="name" label="个人信息" align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                    <p style="font-size:17px;color:#000;">个人信息</p>
                    <p>姓名: {{ scope.row.name }}</p>
                    <p>性别: {{ scope.row.gender }}</p>
                    <p>证件类型: {{ scope.row.idcardType }}</p>
                    <p>证件号码: {{ scope.row.idcard }}</p>
                    <p>登记住所详细地址: {{ scope.row.address }}</p>
                    <p>联系电话: {{ scope.row.phone }}</p>
                    <p>电子邮箱: {{ scope.row.email }}</p>
                    <div slot="reference" class="name-wrapper">
                      <el-tag size="medium">{{ scope.row.name }}</el-tag>
                    </div>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column width="100" label="操作信息" align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                    <p style="font-size:17px;color:#000;">操作信息</p>
                    <p>体检医院名称: {{ scope.row.hospitalName }}</p>
                    <p>体检机设备编号: {{ scope.row.equipment }}</p>
                    <p>审核操作人员: {{ scope.row.operator }}</p>
                    <div slot="reference" class="name-wrapper">
                      <el-tag size="medium">悬停查看</el-tag>
                    </div>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column width="100" label="医院审核" align="center">
                <template slot-scope="scope">
                  <el-tag :type="scope.row.hospitalState === '审核通过' ? 'success' : 'danger'" disable-transitions>{{scope.row.hospitalState}}</el-tag>
                </template>
              </el-table-column>
              <el-table-column width="100" label="驾校审核" align="center">
                <template slot-scope="scope">
                  <el-tag :type="scope.row.carState === '审核通过' ? 'success' : 'danger'" disable-transitions>{{scope.row.carState}}</el-tag>
                </template>
              </el-table-column>
              <el-table-column width="80" label="缴费状态" align="center">
                <template slot-scope="scope">
                  <i :class="scope.row.phoneCost === '已缴费' ? 'el-icon-success' : 'el-icon-error'"></i>
                </template>
              </el-table-column>
              <el-table-column width="80" label="上传结果" align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                    <p style="font-size:17px;color:#000;">详情</p>
                    <p>代号: {{ scope.row.uploadDh }}</p>
                    <p>说明: {{ scope.row.uploadExplain }}</p>
                    <div slot="reference" class="name-wrapper">
                      <el-tag :type="scope.row.uploadState === '成功' ? 'success' : 'danger'" disable-transitions>{{scope.row.uploadState}}</el-tag>
                    </div>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column width="180" prop="examinationTime" label="体检日期" align="center"></el-table-column>
              <el-table-column width="180" prop="examineTime" label="审核时间" align="center"></el-table-column>
              <el-table-column width="170" fixed="right" label="操作" align="center">
                <template slot-scope="scope">
                  <el-tooltip content="详情" placement="top">
                    <el-button @click="openVisible2(scope.$index, scope.row)" type="primary" icon="el-icon-s-order"
                      circle></el-button>
                  </el-tooltip>
                  <el-tooltip content="补传" placement="top">
                    <el-button @click="SeeClick(scope.$index, scope.row)" type="success" icon="el-icon-upload2" circle></el-button>
                  </el-tooltip>
                  <el-tooltip content="移入黑名单" placement="top">
                    <el-button @click="openVisible(scope.$index, scope.row)" type="danger" icon="el-icon-heimingdan"
                      circle></el-button>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination background layout="prev, pager, next" :total="1000" style="margin-top:10px;"></el-pagination>
          </el-col>
        </el-row>
      </el-col>
    </el-card>
    <!-- 移入黑名单 -->
    <el-dialog title="移入黑名单" :visible.sync="dialogVisible">
      <div>姓名：{{SeeClickData.name}}</div>
      <div>证件类型：{{SeeClickData.type}}</div>
      <div>证件号码：{{SeeClickData.idcard}}</div>
      <el-form>
        <el-form-item label="拉黑原因:">
          <el-input type="textarea" v-model="reason"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 认</el-button>
      </div>
    </el-dialog>
    <!-- 信息下载 -->
    <el-dialog width="40%" title="下载筛选" :visible.sync="dialogVisible5">
      <el-form label-width="100px">
          <el-form-item label="上传结果">
            <el-select v-model="downLoad.value1">
              <el-option label="成功" value="成功"></el-option>
              <el-option label="失败" value="失败"></el-option>
              <el-option label="未上传" value="未上传"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="日期选择">
            <el-date-picker v-model="downLoad.value2" type="daterange" range-separator="至" start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        <el-col :span="15">
          <el-form-item label="审核人员">
            <el-input v-model="downLoad.value3" placeholder="选填"></el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible5 = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible5 = false">下 载</el-button>
      </div>
    </el-dialog>
    <!-- 详情弹框 -->
    <el-dialog title="体检详情" :visible.sync="dialogVisible2" center :fullscreen="true">
      <el-row :gutter="10">
        <el-col :span="8">
          <el-card>
            <div class="boxTopTitle">人员信息</div>
            <ul class="topBoxs">
              <li class="boxss" @click="openVisible3">
                <div>
                  <img :src="SeeClickData.tjPhoto" alt="图片加载失败">
                </div>
                <p class="demonstrations">体检拍照</p>
              </li>
              <li class="boxss" @click="openVisible3">
                <div>
                  <img :src="SeeClickData.idPhoto" alt="图片加载失败">
                </div>
                <p class="demonstrations">身份证照片</p>
              </li>
              <li class="boxss">
                <div>
                  <el-progress type="circle" :width="112" :percentage="SeeClickData.similar"></el-progress>
                </div>
                <p class="demonstrations">相似度</p>
              </li>
            </ul>
            <div class="textItem"></div>
            <div class="textItem">
              <div>办理业务类型：</div>
              <div>{{SeeClickData.type}}</div>
            </div>
            <div class="textItem">
              <div>流水号：</div>
              <div>{{SeeClickData.serial}}</div>
            </div>
            <div class="textItem">
              <div>车型：</div>
              <div class="textCar">{{SeeClickData.car}}</div>
            </div>
            <div class="textItem">
              <div>姓名：</div>
              <div>{{SeeClickData.name}}</div>
            </div>
            <div class="textItem">
              <div>性别：</div>
              <div>{{SeeClickData.gender}}</div>
            </div>
            <div class="textItem">
              <div>证件类型：</div>
              <div>{{SeeClickData.idcardType}}</div>
            </div>
            <div class="textItem">
              <div>证件号码：</div>
              <div>{{SeeClickData.idcard}}</div>
            </div>
            <div class="textItem">
              <div>联系地址：</div>
              <div>{{SeeClickData.address}}</div>
            </div>
            <div class="textItem">
              <div>联系电话：</div>
              <div>{{SeeClickData.phone}}</div>
            </div>
            <div :class="SeeClickData.carState=='审核通过'?'textBot':'textBot1'">{{SeeClickData.carState}}</div>
